.nav {
  position: fixed;
  top: 0;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 20px;
  height: 30px;

  z-index: 1;

  -webkit-transition-timing-function: ease-in;

       -o-transition-timing-function: ease-in;

          transition-timing-function: ease-in;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.nav__logo {
  width: 80px;
  position: fixed;
  left: 20px;
  -o-object-fit: contain;
     object-fit: contain;
  cursor: pointer;
}

.nav__avatar {
  width: 30px;
  -o-object-fit: contain;
     object-fit: contain;
  position: fixed;
  right: 20px;
  cursor: pointer;
}
.nav__black {
  background-color: black;
}



