.profilescreen {
  height: 100vh;
  color: white;
}

.profilescreen_body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 8%;
  max-width: 800px;
}

.profilescreen_info {
  display: -ms-flexbox;
  display: flex;
}

.profilescreen_body > h1 {
  font-size: 60px;
  font-weight: 400;
  border-bottom: 1px solid #282c2d;
  margin-bottom: 20px;
}

.profilescreen_info > img {
  height: 120px;
}

.profilescreen_details {
  color: white;
  margin-left: 25px;
  -ms-flex: 1 1;
      flex: 1 1;
}

.profilescreen_details > h2 {
  background-color: gray;
  padding: 15px;
  font-size: 15px;
  padding-left: 20px;
}

.profilescreen_signout {
  padding: 10px 20px;
  font-size: 1rem;
  margin-top: 5%;
  width: 100%;
  color: #fff;
  background-color: #e50914;
  border: none;
  cursor: pointer;
}
.profilescreen_plans > h3 {
  border-bottom: 1px solid #282c2d;
  padding-bottom: 10px;
}
.profilescreen_plans {
  margin-top: 20px;
}



