.signupscreen{
    max-width: 300px;
    padding: 70px;
    margin-left: auto;
    margin-right: auto;
    background: rgba(0,0,0,0.85);
}

.signupscreen > form{
    display: grid;
    -ms-flex-direction: column;
        flex-direction: column;
}

.signupscreen> form > input {
    padding: 10px;
    outline-width: 0;
    height: 40px;
    border: none;
    border-radius: 5px;
    margin-bottom: 14px;

}
.signupscreen > form >button {
    padding:16px 20px;
    font-size: 1rem;
    color: #fff;
    border-radius: 5px;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
    margin-top: 20px;
}
.signupscreen > form >h1{
    text-align: left;
    margin-bottom: 25px;
}
.signupscreen_gray{
    color:grey;
}
.signupscreen > form > h4{
    text-align: left;
    margin-top: 30px;
}
.signupscreen_link:hover{
    cursor: pointer;
    text-decoration: underline ;
     
}


