.banner {
  color: white;
  -o-object-fit: contain;
     object-fit: contain;
  height: 558px;
}
.banner_contents {
  margin-left: 20px;
  padding-top: 190px;
  height: 250px;
}
.banner__title {
  font-size: 3rem;
  font: weight 800px;
  padding-bottom: 0.3rem;
}

.banner__description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.8rem;
  max-width: 360px;
  height: 80px;
}
.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  margin-right: 1rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding: 0.5rem 2rem;
}
.banner__button:hover {
  color: #000;
  background-color: #e6e6e6;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}
.banner__fadeBottom {
  height: 7.4rem;
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(transparent),
    color-stop(rgba(37, 37, 37, 0.5)),
    to(rgb(0, 0, 0))
  );
  background-image: -webkit-linear-gradient(
    top,
    transparent,
    rgba(37, 37, 37, 0.5),
    rgb(0, 0, 0)
  );
  background-image: -o-linear-gradient(
    top,
    transparent,
    rgba(37, 37, 37, 0.5),
    rgb(0, 0, 0)
  );
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.5),
    rgb(0, 0, 0)
  );
}



